//axios功能：统一携带token 请求时  | 跳转 回来时| loading组件 请求时和回来时都要携带| 解决统一导入 | 请求时，统一设定axios中url统一携带/api别名
import axios from 'axios';
import Vue from 'vue';
import router from './router.js'
import vm from '../main.js'
//添加一个请求的拦截器
// 当每次发送请求时，通过拦截器介入，在发送请求前做点事情
axios.interceptors.request.use(function(config) {
  //config 含有发出的请求的配置信息  axios(config)
  
  // 每次都自动携带token
  let token = window.localStorage.getItem('user');//{err:0,msg:'',data:xx,token:string}
  token = token ? JSON.parse(window.localStorage.getItem('user')).token : ''
  // 设置axios发送请求自动携带headers
  config.headers={
    token: token,
  },
  // 通过config.baseURL 解决了请求时，统一设定axios中url统一携带/api别名 
  // 可以通过vue-devtools查看$route中query里上一个页面地址
  config.baseURL =  '/api';
  vm.bLoading=true
  return config;// 撒手放出经过配置的请求
}, function(error) {
  // 发出了错误的请求，拦截
  return Promise.reject(error);
});

// 添加一个响应的拦截器  响应数据回来后，到达目标组件之前，做点事 
axios.interceptors.response.use(function(response) {
  //response  相当于axios请求后的res
  // 响应数据回来后，到达目标组件之前，做点事   res.status   res.data.err == 2
  //校验返回数据，token过期，路由跳转login,传递当前路由地址
  let currentRoute = router.currentRoute.fullPath;//获取当前路由全路径，string
  if(response.data.err===2 && !currentRoute.includes('/login')){
    router.replace({
      path:'/login',
	  // 当我们未登录，访问user页面，会被跳到登录页面，登录页面登录成功后，回到user页面 以下方法解决
      query:{p:currentRoute}
    })
  }
  vm.bLoading=false
  return response;//奔向组件
}, function(error) {
  // 错误的响应，拦截
  return Promise.reject(error);
});

// 将axios添加到Vue的身上，那么在每个vue组件中不需要再import axios from 'axios'; 直接使用this.$axios()，this是当前组件
Vue.prototype.$axios=axios;
window.axios=axios;//×
export default axios;